//  import useEff and useState 
import React, { useEffect, useState } from 'react';
import Axios from 'axios';
import { Link } from '@reach/router';
import 'bootstrap/dist/css/bootstrap.css'

export default props => {

    const getEverything = () => {
        Axios.get("http://localhost:8000/api/incidents")
            .then(res => setIncidents(res.data))
            .catch(err => console.log(err));
    };
// dont forget depedency array
    const [incidents, setIncidents] = useState([]);
    useEffect( () => {
        getEverything();
    }, []);
  

    return(
        <div className="container">
            <h1>EPCR</h1>
            <table className="table table-hover table-striped">
                <tbody>
                    <tr>
                        <th>Incident</th>
                        <th>Type</th>
                        <th>Actions</th>
                    </tr>
                    {
                        incidents.map( incident =>
                            <tr key={incident._id}>
                                <td><Link to={`/incident/${incident._id}`}>{incident.patient_name}</Link></td>
                                <td>{incident.incident_location}</td>
                                <td>
                                    <Link to={`/edit/${incident._id}`} className="btn-link"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-vector-pen" viewBox="0 0 16 16">
                                        <path fill-rule="evenodd" d="M10.646.646a.5.5 0 0 1 .708 0l4 4a.5.5 0 0 1 0 .708l-1.902 1.902-.829 3.313a1.5 1.5 0 0 1-1.024 1.073L1.254 14.746 4.358 4.4A1.5 1.5 0 0 1 5.43 3.377l3.313-.828L10.646.646zm-1.8 2.908-3.173.793a.5.5 0 0 0-.358.342l-2.57 8.565 8.567-2.57a.5.5 0 0 0 .34-.357l.794-3.174-3.6-3.6z"/>
                                        <path fill-rule="evenodd" d="M2.832 13.228 8 9a1 1 0 1 0-1-1l-4.228 5.168-.026.086.086-.026z"/>
                                        </svg>
                                    </Link>
                                </td>
                                
                            </tr>
                        )
                    }
                </tbody>
            </table>
        </div>
    )
}